@font-face {
  font-family: Noto Sans KR;
  src: '../material/font/NotoSansKR-Regular.otf';
}
.detail-body {
  cursor: none;
  width: 100%;
  height: 100%;
  display: block;
  .blocker {
    font: normal 400 16px/1.4 Roboto, Noto Sans KR, Helvetica Neue, Helvetica,
      Arial, sans-serif;
    letter-spacing: 0.8rem;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.749);
    cursor: none;
    .instructions {
      width: 100%;
      height: 100%;

      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      text-align: center;
      font-size: 14px;
      cursor: none;
      img {
        cursor: none;
        opacity: 0.5;
      }

      .text {
        letter-spacing: 0.1em;
        margin-top: 3rem;
        width: 70%;
        font-size: 18px;
        color: rgba(250, 235, 215, 0.762);
        h4 {
          color: #8a9292b6;
        }
        @media only screen and (max-width: 600px) {
          font-size: 14px;
        }
      }
    }
  }
}
